.createWrapper {
    position: fixed;  
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(45deg,#bbddff, #88aed4,#749dc7, #a5a3b7, #9fa1b2, #9bafcc, #7d92b1, #697fa1);
    background-size: 400% 400%;
    animation: gradientMove 5s infinite linear;
}

@keyframes gradientMove {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.createContainer {
    background-color: #edf0f4;
    box-shadow: 6px 5px 7px 0px rgba(0, 0, 0, 0.2), 6px 5px 20px 0px rgba(0, 0, 0, 0.1) inset;
    display: flex;
    width: 55%;
    height: 92.5%;
    flex-direction: column;
    gap:0rem;
    padding: 0rem 3rem 1rem;
    border-radius: 30px;  
   
}

.createNavigation {
    
}
.createForm {
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap:1rem;
    padding: 0rem;
    height: 100%;
}

.createField {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    height: 100%;
}

.createInput {
    padding: 15px 0px 10px 15px;
    background-color: #F3F5F8;
    color: #000000; /* Text color */
    border: 1px solid #B5CBED;
    font-size: 25px;
    border-radius: 10px;
    transition: border 0.1s ease;
}
.createInput::placeholder {
    color: #b4b4b4; /* Change placeholder color */
    opacity: 4;  
}

.createInput:focus {
    border: 2px solid #B5CBED; /* Increase border width */
    outline: none;
}

.createTextArea { 
    padding: 15px 0px 10px 15px;
    color: #000000; /* Text color */
    background-color: #F3F5F8;
    color: #000000; /* Text color */
    outline: none; /* Remove focus outline */
    font-size: 25px;
    border: 1px solid #B5CBED; 
    border-radius: 10px;
    height:85%;
}

.createTextArea::placeholder {
    color: #b4b4b4; /* Change placeholder color */
    opacity: 4;  /* Ensure full visibility */
    font-weight: 0;
  }

.createTextArea:focus {
    border: 2px solid #B5CBED; /* Increase border width */
    outline: none;
}

.createButton {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}

.createSubmit {
    border: none;
    margin: 5px;
    color:#E8EAE5;
    background-color: #749dc7;
    font-size: 20px;
    padding: 10px 25px; 
    margin-right: 10px;
    border-radius: 10px;

}
.createLink {
    color:#E8EAE5;
    text-decoration: none;
    margin:5px;
    background-color: #749dc7;
    font-size: 20px;
    padding: 10px 25px; 
    border-radius: 10px;
    border: 1px solid #B5CBED; 
    border-radius: 10px;
}

@media (max-width: 1250px) {
    .createContainer {
        width: 57%;
        height: 93%;
        padding: 0rem 2.5rem 1rem;   
    }

    .createField {
        gap: 1rem;
    }
    
}
@media (max-width: 1100px) {
    .createContainer {
        width: 65%;
        height: 93%;
        padding: 0rem 2rem 1rem;   
    }
}
@media (max-width: 850px) {
    .createContainer {
        width: 85%;
        height: 90%;
        padding: 0rem 2rem 2rem;   
    }
}

@media (max-width: 680px) {
    .createWrapper {
        background: #6185a9;
        overflow: hidden;
    }
      
    .createContainer {
        background-color: #F3F5F8;
        box-shadow: none;
        width: 100%; 
        height: 98%; 
        flex-direction: column;
        justify-content: center;
        padding: 0rem 1.5rem;
        border-top: 7px solid #6185a9;
        border-bottom: 7px solid #6185a9;
        border-left: 7px solid #6185a9;
        border-right: 7px solid #6185a9;
        border-radius: 0px;    
    }
    
    .createField {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        height: 83%;
    }
    
    .createInput {
        padding: 10px 0px 10px 10px;
        background-color: transparent;
        border: 0.1px solid #B5CBED; 
        font-size: 20px;
    }
    .createInput::placeholder {
        color: #b4b4b4; /* Change placeholder color */
        opacity: 4;  /* Ensure full visibility */
        font-weight: 0;
      }
    
    .createInput:focus {
        border: 2px solid #B5CBED; /* Increase border width */
        outline: none;
    }
    
    .createTextArea { 
        padding: 10px 0px 10px 10px;
        color: #000000;
        background-color: transparent;
        color: #000000; 
        border: none;
        outline: none; 
        font-size: 20px;
        border: 0.1px solid #B5CBED; 
        border-radius: 10px;
        height:80%;
    }
    
    .createTextArea::placeholder {
        color: #b4b4b4; /* Change placeholder color */
        opacity: 4;  /* Ensure full visibility */
        font-weight: 0;
      }
    
    .createTextArea:focus {
        border: 2px solid #B5CBED; /* Increase border width */
        outline: none;
    }
    
    .createButton {
        justify-content: left;
    }

}

@media (max-width: 490px) {
        .createWrapper {
            background: #6185a9;
            overflow: hidden;
        }
          
        .createContainer {
            width: 100%; 
            height: 98%; 
            padding: 0rem 1rem;
            border-top: 7px solid #6185a9;
            border-bottom: 7px solid #6185a9;
            border-left: 7px solid #6185a9;
            border-right: 7px solid #6185a9;
        }
        
        .createField {
            gap: 0.5rem;
            height: 83%;
        }
        
        .createTextArea { 
            height: 90%;
        }
}

@media (max-width: 410px) {
      
    .createContainer {
    }
    
    .createForm {
        gap: 0.7rem;
    }

    .createField {
        gap: 0.5rem;
        height: 85%;
    }
    
}